:root {
  --chart-placeholder-bg: #ffffff1a;
  --dark-overlay: #0006;
  --dropdown-bg: #fffffff2;
  --dropdown-hover-bg: #f1f1f1;
  --dropdown-item-color: #333;
  --dropdown-shadow-color: #0003;
  --footer-background: #1a1a1a;
  --footer-text-color: #ffffffb2;
  --glass-background: #fff3;
  --glass-border-color: #ffffff4d;
  --glass-shadow-color: #0000005e;
  --gradient-end: #9face6;
  --gradient-start: #74ebd5;
  --primary-color: #007bff;
  --scrollbar-thumb-bg: #ffffff4d;
  --scrollbar-track-bg: #ffffff1a;
  --text-color: #fff;
  --user-profile-button-bg: #fff3;
  --user-profile-button-hover-bg: #ffffff4d;
  --font-size-base: 1em;
  --font-size-sm: .8em;
  --font-size-md: 1.1em;
  --font-size-lg: 1.3em;
  --font-size-xl: 1.5em;
  --font-size-xxl: 1.8em;
  --font-size-weather-description: 2.5em;
  --font-size-current-temp: 5em;
  --font-size-feels-like: 1.5em;
  --font-size-chart-title: 1.5em;
  --font-size-forecast-temp: 1.3em;
  --font-size-forecast-icon: 2em;
  --font-size-app-name-mobile: 1.3em;
  --font-size-header-link-mobile: 1em;
  --font-size-date-time-mobile: .9em;
  --font-size-profile-button-mobile: .9em;
  --font-size-weather-description-mobile: 2em;
  --font-size-current-temp-mobile: 4em;
  --font-size-feels-like-mobile: 1.2em;
  --font-size-current-temp-xsm: 3em;
  --font-size-feels-like-xsm: 1em;
  --spacing-xxs: 5px;
  --spacing-xs: 8px;
  --spacing-sm: 10px;
  --spacing-md: 15px;
  --spacing-lg: 20px;
  --spacing-xl: 25px;
  --spacing-xxl: 30px;
  --padding-container: 20px;
  --padding-header: 20px 30px;
  --padding-main: 20px 30px;
  --padding-forecast: 20px 30px 30px;
  --padding-footer: 20px 50px;
  --gap-md: 20px;
  --gap-lg: 25px;
  --gap-sm: 15px;
  --gap-xs: 10px;
  --margin-sm: 5px;
  --margin-md: 10px;
  --margin-lg: 20px;
  --margin-xl: 10px;
  --user-avatar-size: 35px;
  --user-avatar-size-mobile: 30px;
  --chart-placeholder-width: 200px;
  --chart-placeholder-height: 150px;
  --forecast-item-width: 120px;
  --forecast-item-width-mobile: 100px;
  --forecast-item-width-xsm: 90px;
  --footer-max-width: 1200px;
  --dropdown-min-width: 160px;
  --scrollbar-height: 8px;
  --border-radius: 15px;
  --border-radius-sm: 10px;
  --border-radius-pill: 50px;
  --border-radius-circle: 50%;
  --glass-border: 1px solid var(--glass-border-color);
  --glass-box-shadow: 0 8px 32px 0 var(--glass-shadow-color);
  --dropdown-box-shadow: 0px 8px 16px 0px var(--dropdown-shadow-color);
}

body {
  color: var(--text-color);
  background: linear-gradient(135deg, var(--gradient-start) 0%, var(--gradient-end) 100%);
  flex-direction: column;
  min-height: 100vh;
  margin: 0;
  font-family: Segoe UI, Tahoma, Geneva, Verdana, sans-serif;
  display: flex;
  overflow-x: hidden;
}

.dashboard-container {
  padding: var(--padding-container);
  flex-grow: 1;
  justify-content: center;
  align-items: flex-start;
  display: flex;
  position: relative;
}

.glass-rectangle {
  background: var(--glass-background);
  width: 90vw;
  min-height: 90vh;
  backdrop-filter: blur(var(--spacing-md));
  border-radius: var(--border-radius);
  border: var(--glass-border);
  box-shadow: var(--glass-box-shadow);
  flex-direction: column;
  display: flex;
  position: relative;
}

.glass-rectangle.sunny, .dashboard-container.sunny {
  background-image: url("sunny-bg.153c7f09.jpg");
  background-position: center;
  background-size: cover;
}

.glass-rectangle.cloudy, .dashboard-container.cloudy {
  background-image: url("cloudy-bg.dbb168d9.jpg");
  background-position: center;
  background-size: cover;
}

.glass-rectangle.partly-cloudy, .dashboard-container.partly-cloudy {
  background-image: url("partly-cloudy-bg.b71add2c.jpg");
  background-position: center;
  background-size: cover;
}

.glass-rectangle.stormy, .dashboard-container.stormy {
  background-image: url("stormy-bg.4527fa87.avif");
  background-position: center;
  background-size: cover;
}

.glass-rectangle:before {
  content: "";
  background: var(--dark-overlay);
  z-index: 1;
  position: absolute;
  inset: 0;
}

.dashboard-header {
  z-index: 10;
  position: relative;
}

.dashboard-main, .main, .forecast-section {
  z-index: 2;
  position: relative;
}

.dashboard-header {
  padding: var(--padding-header);
  color: var(--text-color);
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.app-name {
  font-size: var(--font-size-xxl);
  margin: 0;
  font-weight: 600;
}

.header-middle ul {
  gap: var(--spacing-xl);
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.header-middle a {
  color: var(--text-color);
  font-size: var(--font-size-lg);
  text-decoration: none;
  transition: color .3s;
}

.header-middle a:hover {
  color: var(--primary-color);
}

.header-right {
  align-items: center;
  gap: var(--gap-md);
  display: flex;
}

.date-time {
  font-size: var(--font-size-base);
  opacity: .8;
}

.user-menu {
  position: relative;
}

.user-profile-button {
  background: var(--user-profile-button-bg);
  border-radius: var(--border-radius-pill);
  padding: var(--spacing-xs) var(--spacing-md);
  align-items: center;
  gap: var(--spacing-sm);
  cursor: pointer;
  color: var(--text-color);
  font-size: var(--font-size-base);
  border: none;
  transition: background .3s;
  display: flex;
}

.user-profile-button:hover {
  background: var(--user-profile-button-hover-bg);
}

.user-avatar {
  width: var(--user-avatar-size);
  height: var(--user-avatar-size);
  border-radius: var(--border-radius-circle);
  object-fit: cover;
  border: 2px solid var(--text-color);
}

.user-info {
  text-align: left;
  flex-direction: column;
  display: flex;
}

.user-name {
  font-weight: 600;
}

.user-role {
  font-size: var(--font-size-sm);
  opacity: .7;
}

.arrow-down {
  margin-left: var(--spacing-xxs);
}

.dropdown-content {
  background-color: var(--dropdown-bg);
  min-width: var(--dropdown-min-width);
  box-shadow: var(--dropdown-box-shadow);
  border-radius: var(--border-radius-sm);
  margin-top: var(--margin-xl);
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  overflow: hidden;
}

.dropdown-content a {
  color: var(--dropdown-item-color);
  padding: var(--spacing-md) var(--spacing-md);
  text-decoration: none;
  transition: background-color .3s;
  display: block;
}

.dropdown-content a:hover {
  background-color: var(--dropdown-hover-bg);
}

.user-menu.active .dropdown-content {
  display: block;
}

.dashboard-main {
  padding: var(--padding-main);
  gap: var(--gap-md);
  flex-grow: 1;
  grid-template-columns: 1fr 2fr 1fr;
  display: grid;
}

.main-left {
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  display: flex;
}

.weather-description {
  font-size: var(--font-size-weather-description);
  margin-bottom: var(--spacing-sm);
  font-weight: 300;
}

.temperature .current-temp {
  font-size: var(--font-size-current-temp);
  font-weight: 700;
  line-height: 1;
}

.temperature .feels-like {
  font-size: var(--font-size-feels-like);
  opacity: .8;
  margin-top: var(--spacing-xxs);
}

.main-right {
  text-align: right;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-end;
  display: flex;
}

.wind-status-chart h3 {
  margin-bottom: var(--spacing-sm);
  font-size: var(--font-size-chart-title);
  font-weight: 500;
}

.chart-placeholder {
  background: var(--chart-placeholder-bg);
  border-radius: var(--border-radius);
  padding: var(--padding-container);
  width: var(--chart-placeholder-width);
  height: var(--chart-placeholder-height);
  color: var(--text-color);
  justify-content: center;
  align-items: center;
  display: flex;
}

.time-of-day p {
  margin: var(--margin-sm) 0;
  font-size: var(--font-size-md);
}

.forecast-section {
  padding: var(--padding-forecast);
  flex-direction: column;
  display: flex;
}

.forecast-section h2 {
  margin-top: 0;
  margin-bottom: var(--margin-lg);
  font-size: var(--font-size-xxl);
  font-weight: 600;
}

.forecast-list {
  gap: var(--gap-md);
  padding-bottom: var(--spacing-sm);
  -webkit-overflow-scrolling: touch;
  display: flex;
  overflow-x: auto;
}

.forecast-list::-webkit-scrollbar {
  height: var(--scrollbar-height);
}

.forecast-list::-webkit-scrollbar-track {
  background: var(--scrollbar-track-bg);
  border-radius: var(--border-radius-sm);
}

.forecast-list::-webkit-scrollbar-thumb {
  background: var(--scrollbar-thumb-bg);
  border-radius: var(--border-radius-sm);
}

.forecast-item {
  flex: 0 0 var(--forecast-item-width);
  background: var(--chart-placeholder-bg);
  border-radius: var(--border-radius);
  padding: var(--spacing-md);
  text-align: center;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.forecast-day {
  margin-bottom: var(--spacing-xxs);
  font-weight: 600;
}

.forecast-icon {
  font-size: var(--font-size-forecast-icon);
  margin: var(--spacing-sm) 0;
}

.forecast-temp {
  font-size: var(--font-size-forecast-temp);
  font-weight: 700;
}

.site-footer {
  background: var(--footer-background);
  color: var(--footer-text-color);
  padding: var(--padding-footer);
  text-align: center;
  width: 100%;
}

.footer-content {
  max-width: var(--footer-max-width);
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  display: flex;
}

.footer-content p {
  margin: var(--margin-sm) 0;
}

.footer-nav ul {
  gap: var(--gap-md);
  flex-wrap: wrap;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.footer-nav a {
  color: var(--footer-text-color);
  text-decoration: none;
  transition: color .3s;
}

.footer-nav a:hover {
  color: var(--text-color);
}

.visit-counter {
  font-weight: bold;
}

#avatar-preview {
  background-image: url("user-avatar.d3abfc74.jpg");
}

.fade-out {
  animation: .5s ease-out forwards fadeOut;
}

@keyframes fadeOut {
  from {
    opacity: 1;
    transform: scale(1);
  }

  to {
    opacity: 0;
    border: none;
    height: 0;
    margin-bottom: 0;
    padding-top: 0;
    padding-bottom: 0;
    overflow: hidden;
    transform: scale(.95);
  }
}

@media (width <= 1200px) {
  .glass-rectangle {
    width: 95vw;
  }
}

@media (width <= 992px) {
  .dashboard-header {
    gap: var(--gap-sm);
    flex-direction: column;
  }

  .header-middle ul {
    gap: var(--gap-sm);
  }

  .dashboard-main {
    padding: var(--spacing-md);
    grid-template-rows: auto auto auto;
    grid-template-columns: 1fr;
  }

  .main-left, .main-right {
    text-align: center;
    align-items: center;
  }

  .wind-status-chart {
    align-items: center;
  }

  .forecast-section {
    padding: var(--spacing-md);
  }

  .footer-content {
    gap: var(--gap-sm);
    flex-direction: column;
  }

  .footer-nav ul {
    gap: var(--spacing-sm);
    flex-direction: column;
  }
}

@media (width <= 768px) {
  .app-name {
    font-size: var(--font-size-xl);
  }

  .header-middle a {
    font-size: var(--font-size-md);
  }

  .date-time {
    font-size: var(--font-size-sm);
  }

  .user-profile-button {
    padding: var(--spacing-xs) var(--spacing-md);
    font-size: var(--font-size-sm);
  }

  .user-avatar {
    width: var(--user-avatar-size-mobile);
    height: var(--user-avatar-size-mobile);
  }

  .weather-description {
    font-size: var(--font-size-weather-description-mobile);
  }

  .temperature .current-temp {
    font-size: var(--font-size-current-temp-mobile);
  }

  .temperature .feels-like {
    font-size: var(--font-size-feels-like-mobile);
  }

  .forecast-item {
    flex: 0 0 var(--forecast-item-width-mobile);
    padding: var(--spacing-sm);
  }
}

@media (width <= 480px) {
  .glass-rectangle {
    width: 100vw;
    height: 100vh;
    box-shadow: none;
    border-radius: 0;
  }

  .dashboard-header {
    padding: var(--spacing-md) var(--spacing-sm);
  }

  .app-name {
    font-size: var(--font-size-app-name-mobile);
  }

  .header-middle ul {
    gap: var(--spacing-sm);
  }

  .header-middle a {
    font-size: var(--font-size-header-link-mobile);
  }

  .header-right {
    gap: var(--spacing-sm);
    z-index: 2;
    flex-direction: column;
    align-items: flex-end;
  }

  .dashboard-main {
    padding: var(--spacing-sm);
  }

  .current-temp {
    font-size: var(--font-size-current-temp-xsm) !important;
  }

  .feels-like {
    font-size: var(--font-size-feels-like-xsm) !important;
  }

  .wind-status-chart, .time-of-day {
    margin-top: var(--spacing-md);
  }

  .forecast-list {
    gap: var(--spacing-sm);
  }

  .forecast-item {
    flex: 0 0 var(--forecast-item-width-xsm);
  }

  .site-footer {
    padding: var(--spacing-md);
  }
}
